.block-comment {
  min-width: 280px;
  min-height: 192px;
  padding: 25px 10px 26px 17px;
  background-color: #f2f2f2;
}

.block-comment__name {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 24px;
  color: #283645;
  display: flex;
  align-items: center;
  text-align: center;

  span {
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 300;
    font-size: 12px;
    line-height: 24px;
  }
}

.block-comment__text {
  margin: 0;
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: #283645;
  margin-bottom: 23px;
}

.liked {
  display: flex;
  align-items: center;

  svg {
    margin-right: 18px;
  }

  p {
    margin: 0;
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 24px;
    display: flex;
    align-items: center;
    text-align: right;
    text-transform: uppercase;
    color: #d22856;
  }
}
